@import url(http://weloveiconfonts.com/api/?family=fontawesome);

/* fontawesome */
[class*="fontawesome-"]:before {
  font-family: 'FontAwesome', sans-serif;
}

* {
    box-sizing: border-box;
}

a {
	text-decoration: none;
}

ul {
margin: 0;
}

body, html {
  width: 100%;
  height: 100%;
	font-family: sans-serif;
	font-size: 16px;
	background-color: #f2f2f2;
}

.clear-fix:after {
	content: '';
	display: table;
	clear: both;
}

.button {
	cursor: pointer;
	padding: 0;
}

header {
		min-height: 48px;
		background-color: white;
		position: fixed;
		top: 0;
		width: 100%;
		z-index: 999;
	}
	
	.header-inner {
		max-width: 1270px;
    margin: 0 auto;
		height: 48px;
		background-color: white;
	}
	
	.logo {
		width: 118px;
		height: 48px;
		padding-left: 10px;
		float: left;
	}
	
	.logo img {
		max-height: 23px;
		max-width: 108px;
		margin-top: 13px;
	}
	
	.buttons-list{
		float: right;
		font-size: 0px;
	}
	
	.buttons-list .button {
		font-size: 20px;
		background-color: white;
		color: dimgray;
		transition: background-color .3s ease-out;
		display: inline-block;
		
	}

  .buttons-list .button span {
    display: block;
		padding: 12px 15px;
	}
	
.buttons-list .button:hover {
    outline-width: 0px;
    outline-color: #0072C6;
    background-color: #f2f2f2;
    transition: background-color 0s;
}
	
	.header-menubar{
		position: absolute;
		z-index: 1;
		background-color: #f2f2f2;
		width: 100%;
		top: 47px;
		right: 0px;
		transform: translateX(1800px);
		transition: transform .4s ease-in;
	}
	
	header .active {
		transform: translateX(0);
	}
	
	.menu-list {
		list-style: none;
		margin-top: 0;
		padding-left: 0;
	}
	.menu-list li {
		width: 100%;
	}
	
	.menu-list a {
		display: block;
		width: 100%;
		padding: 15px 10px;
		color: #505050;
		text-decoration: none;
		cursor: pointer;
	}
	
	.sub-menu {
		width: 100%;
		max-height: 0;
		overflow: hidden;
		list-style: none;
    padding-left: 0px;
		transition: max-height .1s ease-out;
	}
	
	.slide-down {
	max-height: 500px;
		overflow: visible;
	}

	.header-menubar li {
		background-color: #f2f2f2;
		transition: background-color .3s ease-out; 
	}
	
	.header-menubar li:hover {
		background-color: #e5e5e5;
		transition: background-color 0s; 
	}
	
	.header-menubar .lighten:hover {
		background-color: #f2f2f2;
	}
	
	.sub-menu li {
		background-color: #e5e5e5;
		transition: background-color .3s ease-out;
	}
	
	.sub-menu li:hover {
		background-color: #d9d9d9;
		transition: background-color 0s;
	}
	
 .fontawesome-angle-down {
		float: right;
	}
	
	
	.sub-menu .slide-down li {
	background-color: #d9d9d9;	
	}
	
	.sub-menu .slide-down li:hover {
	background-color: #ccc;	
	}
	
 .highlight {
	background-color: #f2f2f2 !important; 
	}
	
	[name='searchForm'] {
		display: none;
		text-align: center;
		height: 75px;
		width: 100%;
		padding: 22px 10px;
		background-color: #f2f2f2;
		position: absolute;
		top: 47px;
	}
	
	.active-form {
		display: table;
	}
	
	.buttons-list .sign-in {
		line-height: 22px;
		padding: 15px 15px 10px;
		font-size: 13px;
		color: #505050;
	}
	
	.sign-in a {
		display: block;
		text-decoration: none;
		color: #505050;
	}

	.sign-in a:hover {
	color: #0078d7;
	}
	
	.buttons-list {
		margin-left: 10px;
	}

[name='searchForm'] .submit {
		display: table-cell;
		width: 30px;
		height: 30px;
		background-color: #0078D7;
		color: white;
		text-align: center;
		vertical-align: middle;
		border-style: none;
		outline: none;
	}
	
	[name='searchForm'] .submit:hover {
		background-color: #0060ac;	
	}
	
	[name='searchLine'] {
		display: table-cell;
		height: 30px;
		width: 60%;
		vertical-align: middle;
		outline: none;
		border: none;
		padding-left: 5px;
	}


@media screen and (max-width: 550px) {
	.buttons-list .sign-in {
		display: none;
	}
	
	.menu-list [data-id='item']:first-of-type:hover a {
		color: #0078d7;
	}
	.menu-list li {
		overflow: hidden;
	}
}

@media screen and (min-width: 551px) and (max-width: 920px){
	
	.header-menubar {
		width: 300px;
	}
	
	.menu-list > li:first-of-type {
		display: none;
	}
	.menu-list li {
		overflow: hidden;
	}
	
}

@media screen and (min-width: 921px) {
	
	.header-inner {
		display: -ms-flexbox;
		display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
		-ms-flex-pack: start;
		    justify-content: flex-start;
		padding: 0 40px;
	}
	
	.buttons-list .search,
	.buttons-list .toggle-menu{
		display: none;
	}
	
	.logo {
		-ms-flex-order: 1;
		    order: 1;
	}
	
	.header-menubar {
		position: static;
		height: 48px;
		width: auto;
    transform: none;
    transition: none;
		-ms-flex-order: 2;
		    order: 2;
		margin: 0 auto 0 20px;
		background-color: white;
	}
	
	.menu-list {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-direction: row;
		    flex-direction: row;
		height: 48px;
		-ms-flex-pack: start;
		    justify-content: flex-start;
	}
		
	[name='searchForm'] {
		display: inline-block;
		border: 1px solid gray;
		text-align: center;
    height: 36px;
    width: 256px; 
    padding: 0;
		background-color: white;
		-ms-flex-order: 3;
		    order: 3;
		-ms-flex-item-align: center;
		    align-self: center;
		margin-left: auto;
		position: static
	}
	
	[name='searchLine'] {
		width: 220px;
		height: 34px;
		border-right-color: white;
		padding: 0 10px;
	}
	[name='searchForm'] .submit {
		background-color: white;
		border-left-color: white;
		color: gray;
		height: 34px;
		width: 33px;
		margin-left: -5px;
	}
	
	[name='searchForm'] .submit:hover {
		color: #0072C6;
		background-color: white;
	}
	
	.buttons-list {
		-ms-flex-order: 4;
		    order: 4;
	}
	
	.fontawesome-angle-down {
    float: none;
		margin-left: 5px;
}
	
	.header-menubar li {
    background-color: white;
    transition: background-color .3s ease-out;
	}
	
	.header-menubar li:hover {
		background-color: #f2f2f2;
	}
	
	.header-menubar .sub-menu li {
    background-color: #f2f2f2;;
    transition: background-color .3s ease-out;
	}
	
	.header-menubar .sub-menu li:hover {
		background-color: #e5e5e5;
	}
	
	.sub-menu .sub-menu li {
    background-color: #e5e5e5;
    transition: background-color .3s ease-out;
	}
	
	.sub-menu .sub-menu li:hover {
		background-color: #d9d9d9;
	}
	
	
	.menu-list li {
    width: 100px;
	}
	
	.sub-menu {
    width: 500px;
	}
	.sub-menu li {
		margin-top: -1px;
		display: block;
		position: relative;
    width: 240px;
		background-color: #f2f2f2;
	}
	
	.sub-menu .fontawesome-angle-down {
		display: inline-block;
		transform: rotate(-90deg);
		float: right;
	}
	
	.menu-list > li:first-of-type {
		display: none;
	}
	
  .menu-list > li:nth-child(2) .fontawesome-angle-down {
		margin-left: 25px;
	}

  .menu-list > li:nth-child(4) > a {
		text-align: center;
	}
	
	.sub-menu li > ul  {
		position: absolute;
		display: block;
		width: 0;
		left: 100%;
    top: 0;
		transition: width 0s ease-out .3s;
	}
	.sub-menu li:hover > ul {
		width: 240px;
	}
	
	.menu-list > .sub-menu {
		max-height: 0px;
		overflow: hidden;
		list-style: none;
		transition: max-height .2s ease-out;
	}
	
	.menu-list > .slide-down {
	max-height: 500px;
	}
	
	.sub-menu .sub-menu {
		max-height: 500px;
	}
	
}
.hero {
